<template>
  <div id="app">
    <p v-show="this.x >= this.y">{{ x }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      x: "",
      y: "2021-9-21T10:45:41",
    };
  },
  mounted() {
      this.x = this.currentDateTime();
  },
  methods: {
    currentDateTime() {
      const current = new Date();
      const date =
        current.getFullYear() +
        "-" +
        (current.getMonth() + 1) +
        "-" +
        current.getDate();
      const time =
        current.getHours() +
        ":" +
        current.getMinutes() +
        ":" +
        current.getSeconds();
      const dateTime = date + "T" + time;

      return dateTime;
    },
  },
};
</script>
